}

.App {
  background-color: white;
  max-width: 100vw;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow: hidden; }
  .App .shortcuts-section {
    margin-top: 30px;
